body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 5vmin;
}

#calculator {
  display: flex;
  flex-direction: column;
  width: calc(40vw + 30px);
  height: calc(82vh + 80px);
  margin-left: 30vw;
  margin-top: 4vh;
  background-color: lightgrey;
  align-items: center;
  border: 10px solid black;
}

#display {
  width: 90%;
  height: 10vh;
  margin: 1vmin;
  text-align: right;
  padding-right: 2vw;
  line-height: 10vh;
  border: 5px solid grey;
  background-color: darkolivegreen;
  font-size: 6vmin;
}

#grid {
  display: grid;
  grid-template-columns: repeat(4, calc(10vw - 5px));
  grid-template-rows: repeat(5, 14vh);
  grid-template-areas: 
    "ac divide multiply subtract"
    "seven eight nine add"
    "four five six add"
    "one two three equals"
    "zero zero decimal equals";
  gap: 10px;
  margin-top: 1vh;
}

@media screen and (max-width:  600px) {
  body {
    font-size: 7vmin;
  }
  #calculator {
    width: calc(70vw + 30px);
    height: calc(72vh + 80px);
    margin-left: 1vw;
    margin-top: 8vh;
  }
  #display {
    font-size: 8vmin;
  }
  #grid {
    grid-template-columns: repeat(4, calc(17.5vw - 5px));
    grid-template-rows: repeat(5, calc(12vh + 5px));
  }
}

@media screen and (max-width: 1024px) {
  #calculator {
    width: calc(70vw + 30px);
    height: calc(72vh + 80px);
    margin-left: 10vw;
    margin-top: 8vh;
  }
  #grid {
    grid-template-columns: repeat(4, calc(17.5vw - 5px));
    grid-template-rows: repeat(5, 12vh);
  }
}

#clear {
  grid-area: ac;
}

#divide {
  grid-area: divide;
}

#multiply {
  grid-area: multiply;
}

#subtract {
  grid-area: subtract;
}

#seven {
  grid-area: seven;
}

#eight {
  grid-area: eight;
}

#nine {
  grid-area: nine;
}

#add {
  grid-area: add;
  line-height: 30vh;
}

#four {
  grid-area: four;
}

#five {
  grid-area: five;
}

#six {
  grid-area: six;
}

#one {
  grid-area: one;
}

#two {
  grid-area: two;
}

#three {
  grid-area: three;
}

#equals {
  grid-area: equals;
  line-height: 30vh;
}

#zero {
  grid-area: zero;
}

#decimal {
  grid-area: decimal;
}

.pad {
  background-color: #778899;
  text-align: center;
  line-height: 15vh;

}

.pad:hover {
  cursor: pointer;
  background-color: #667788;
}